<template>
  <q-layout class="bg-bg">
    <q-layout-header>
      <q-toolbar>
        <q-btn flat icon="arrow_back_ios" @click="$router.go(-1)"/>
        <q-toolbar-title>营销订单</q-toolbar-title>
      </q-toolbar>
    </q-layout-header>

    <q-page-container>
      <q-page padding>
        <q-search color="white" inverted-light v-model="search" placeholder="输入订单号搜索..."/>

        <q-list no-border>
          <div v-for="(item,index) in [1]" :key="index">
            <!-- <q-list-header>{{item.header}}</q-list-header> -->
            <q-item v-for="(item,index) in items" :key="item.Guid" :to="'/order/'+item.Guid">
              <q-item-main>
                <q-item-tile label>
                  <span>{{index % 2 ? '传统销售' : '网络销售'}}</span>
                  {{item.SerialNumber}}
                </q-item-tile>
                <q-item-tile sublabel>{{item.CustomerSaleName}}</q-item-tile>
              </q-item-main>
              <q-item-side right>
                <q-item-tile label v-if="index % 2">{{index % 2 ? '领导审批' : '出库'}}</q-item-tile>
                <q-item-tile label v-if="!(index % 2)">
                  <span class="red-dot"></span>
                </q-item-tile>
                <q-item-tile sublabel>{{'￥' + item.TotalFixedPrice}}</q-item-tile>
              </q-item-side>
            </q-item>
          </div>
        </q-list>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<style scoped>
.q-item {
  background-color: #fff;
  border-radius: 3px;
}

.q-item + .q-item {
  margin-top: 8px;
}

.red-dot {
  --radius: 8px;
  display: inline-block;
  width: var(--radius);
  height: var(--radius);
  background-color: red;
  border-radius: 50%;
}
</style>

<script>
import axios from "axios";

export default {
  name: "orderList",
  data() {
    return {
      search: "",
      items: []
    };
  },
  mounted() {
    axios
      .get("https://api.dev.comemind.com/market/market/list")
      .then(response => {
        this.items = response.data.data;
      });
  }
};
</script>
